@charset "utf-8";
html,
body,
:root {
  width: 100%;
  height: 100%;
  background: #fff;
  font-family: 'Cabin', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
}
::selection {
  color: #fff;
  background: rgba(78,84,200,0.267);
}
* {
  padding: 0;
  margin: 0;
  position: relative;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
}
*.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
*.noselect {
  user-select: none;
}
*.nodisplay {
  display: none;
}
img {
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
}
a {
  text-decoration: none;
  color: inherit;
}
a:visited {
  color: inherit;
}
section.navigation {
  height: 5vh;
  width: 100%;
  flex-direction: column;
  background: #f3f1fb;
}
@media only screen and (max-width: 800px) {
  section.navigation {
    height: 7vh;
  }
}
section.navigation .container {
  width: 90%;
  max-width: 1000px;
  height: 100%;
  justify-content: space-between;
}
section.navigation .color-bar {
  height: 3px;
  width: 100%;
  background: linear-gradient(to right, #4e54c8, #8f94fb);
}
section.navigation a {
  padding: 0 20px;
  font-weight: bold;
  color: #4e54c8;
  font-size: 1.3em;
}
section.navigation input {
  padding: 0 20px;
  font-weight: bold;
  font-family: "Cabin";
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
}
